body {
  // background-color: red;
}

* {
  box-sizing: border-box;
}

.App {
  // border: 1px solid red;
  padding: 24px;

  h1 {
    font-family: 'Inter', sans-serif;
    font-weight: 600;
    font-size: 24px;
    line-height: 110%;
    color: #393e4b;
    text-align: center;
  }

  .form {
    width: 100%;
    display: flex;
    flex-direction: column;

    input[type='text'] {
      border: 1px solid red;
      padding: 18px;
      font-size: 16px;
      width: 100%;
      border: 2px solid #d0d5d9;
      box-sizing: border-box;
      border-radius: 4px;
      margin-top: 8px;
    }

    label {
      margin-top: 18px;
      font-family: Inter;
      font-size: 14px;
      line-height: 120%;
    }
  }
}
